<template>
  <div class="hello">
    <div v-if="isInstallMetaMask == true">메타마스크 설치됨
      <!--메타마스크 연결 버튼-->
      <div v-if="currentAddress == null">
        <button v-on:click="connectMetaMask">메타 마스크 연결</button>
      </div>
      <div v-if="currentAddress != null">
        현재 계정 주소: {{currentAddress}} 
        <div v-if="connectedContract == null">
          <button v-on:click="connectContract">컨트랙트 연결</button>
        </div>
        <div v-else>
          <!-- ownerOf 함수를 호출해보자 -->
          <div>
            <button v-on:click="ownerOf">ownerOf</button>
            <!--v-model은 태그의 값이 해당 변수에 저장됨-->
            <input type="text" placeholder="tokenId" v-model="ownerOf_tokenId"/>
            <div v-if="ownerOf_result != null">
              ownerOf: {{ownerOf_result}}
            </div>
          </div>
          <!--balanceOf 함수를 호출해보자: 내가 mint한 토큰 개수(opensea)-->
          <div v-if="ownerOf_result != null">
            <button v-on:click="balanceOf">balanceOf</button>
            <input type="text" placeholoder="owner address" v-model="balanceOf_ownerAddress"/>
            <!--<input type="text" placeholoder="owner address" v-model="ownerOf_result"/>-->
            <div v-if="balanceOf_result != null">
              balanceOf: {{balanceOf_result}}
            </div>
          </div>
          <!--mint를 구현해보자 -->
          <div>
            <button v-on:click="mint">Mint!</button>
            <input type="text" placeholder="address(to)" v-model="mint_to"/>
            <input type="text" placeholder="tokenId" v-model="mint_tokenId"/>
            <input type="text" placeholder="uri" v-model="mint_uri"/>  
            <div v-if="mint_result != null">
              mint: {{mint_result}}
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-else>메타마스크 설치안됨, 설치해주세요!</div>
  </div>
</template>

<script>
//컨트랙트 연결하기 위해 필요
import {ethers} from "ethers";
import abi from "../assets/abi.json";

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    //변수 초기화
    return {
      isInstallMetaMask: window.ethereum != null && window.ethereum.isMetaMask == true,
      currentAddress: null,
      connectedContract : null,
      //ownerOf에 필요한 변수
      ownerOf_tokenId: null,
      ownerOf_result: null,
      //balanceOf에 필요한 변수
      balanceOf_ownerAddress: null,
      balanceOf_result: null,
      //mint에 필요한 변수
      mint_result: null,
      mint_to: null,
      mint_tokenId: null,
      mint_uri: null
    }
  },
  methods:{
    //metamask 연동 함수 구현
    async connectMetaMask() {
      console.log("connectMetaMask 함수 호출");
      var account = await window.ethereum.request({method: 'eth_requestAccounts'});

      if(account.length > 0){
        this.currentAddress = account[0];
        //console.log(this.currentAddress);
      }else{
        //메타마스크에 계정이 없는 경우
        console.log("계정이 없음");
      }
    },
    async connectContract() {
      console.log("connectContract 함수 호출");
      //var contractAddress = '0xA473b6EfB7b45fE78326555ac1e769ab26b1D506';
      var contractAddress = '0x22a3D23710c889aAC958De4fC7EB088Bb251Ac16';
      var provider = new ethers.providers.Web3Provider(window.ethereum);
      //가짜 서명
      var tempSigner = provider.getSigner();
      //접속을 위해서는 해당 컨트랙스 주소, abi, 서명이 필요
      var connectedContract = new ethers.Contract(contractAddress, abi ,tempSigner);
      console.log(connectedContract);
      this.connectedContract = connectedContract;

    },
    async ownerOf() {
      console.log("ownerOf 함수 호출");
      var ownerAddress = await this.connectedContract.ownerOf(Number(this.ownerOf_tokenId));
      this.ownerOf_result = ownerAddress;
    },
    async balanceOf() {
      console.log("balanceOf 함수 호출");
      //var balance = await this.connectedContract.balanceOf(this.ownerOf_result);
      var count = await this.connectedContract.balanceOf(this.balanceOf_ownerAddress);
      this.balanceOf_result = count;
    },
    async mint() {
      try{
        var transaction = await this.connectedContract.mint(
          this.mint_to,
          this.mint_tokenId,
          this.mint_uri
        );
        this.mint_result = "생성 대기 중";
        await transaction.wait();
        this.mint_result = transaction;
        console.log(transaction);
      }catch(e) {
      console.log(e);
      this.mint_result = e;
    }
  }
}
}
</script>

